"use client";
import ModalContainer, { closingTransition } from "@/components/ModalContainer";
import {
  Dispatch,
  SetStateAction,
  useCallback,
  useEffect,
  useState,
} from "react";
import { useMemo } from "react";
import Console from "@/utils/console";
import axios from "@/utils/api-handler/axios";
import IconSkeleton from "@/components/icons/IconSkeleton";
import GoogleIcons from "@/components/icons/GoogleIcons";
import SpinLoader from "@/components/preloader/SpinLoader";
import { isNull, notNull } from "@/utils/more";
import Image from "next/image";
import $$ from "@/utils/global-variables";
import { useDebouncedCallback } from "use-debounce";
import api from "@/app/api/endpoints";
import { mutate } from "@/utils/api-handler/swr";

export default function AddChairmanModal({
  setOpenModal,
  department,
}: {
  setOpenModal: Dispatch<SetStateAction<boolean>>;
  department: {
    id: number;
    full: string;
  };
}) {
  return useMemo(
    () => (
      <_AddChairmanModal setOpenModal={setOpenModal} department={department} />
    ),
    [setOpenModal, department]
  );
}

function _AddChairmanModal({
  setOpenModal,
  department,
}: {
  setOpenModal: Dispatch<SetStateAction<boolean>>;
  department: {
    id: number;
    full: string;
  };
}) {
  Console.component("AddChairmanModal");

  const closeModal = useCallback(() => {
    closingTransition(() => {
      setOpenModal((prev) => !prev);
    });
  }, [setOpenModal]);

  const [searchResult, setSearchResult] = useState<
    | {
        id: number;
        email: string;
        name: string;
        img: string;
      }[]
    | undefined
  >(undefined);
  const [isSearching, setIsSearching] = useState<boolean>(false);
  const [selectedId, setSelectedId] = useState<number | undefined>(undefined);

  //   Search users
  const debounced = useDebouncedCallback(async (value) => {
    if (isNull(value)) {
      setIsSearching(false);
      return;
    } else setIsSearching(true);

    const res = await axios.get(api.searchChairman + value.trim());
    if (res.status === 200) setSearchResult(res.data);
    setIsSearching(false);
  }, 1000);

  //   Do assign chairman
  useEffect(() => {
    if (!selectedId) return;
    (async () => {
      await axios.post(api.addChairman, {
        chairmanId: selectedId,
        departmentId: department.id,
      });
      await mutate(api.readFacultyDeptProgramData);
      closeModal();
    })();
  }, [selectedId, department.id, closeModal]);

  return (
    <ModalContainer>
      <div className="w-[500px] px-8 py-12">
        {/* navbar */}
        <div className="mb-4 flex justify-between items-start gap-2">
          <div>
            <div className="mb-1 text-2xl fontGs">Assign Chairman</div>
            <div className="text-13 text-gray-600">
              Department: {department.full}
            </div>
          </div>
          <IconSkeleton title="Close" onClick={closeModal}>
            <GoogleIcons.close className="fill-gray-500" />
          </IconSkeleton>
        </div>

        {/* Middle */}
        <div>
          {/* Search bar */}
          <div className=" flex justify-between items-center border-b border-gray-300">
            <div className="flex-1">
              <input
                type="text"
                placeholder="Type email or name here"
                className="w-full px-2 py-3 dark:bgBlack"
                onChange={(e) => {
                  const val = e.target.value;
                  if (notNull(val)) setIsSearching(true);
                  debounced(val);
                }}
                spellCheck={false}
                autoComplete="off"
              />
            </div>
            {isSearching && <SpinLoader size={28} />}
          </div>

          {/* Search result */}
          <div className="h-[250px] mt-4 space-y-1 overflow-y-auto">
            {!searchResult ? (
              <div></div>
            ) : isNull(searchResult) ? (
              <div className="w-full h-full flex flex-col justify-center items-center">
                <div className="mb-4 text-gray-300 text-6xl">(^-^*)</div>
                <div className="text-gray-400">No data found</div>
              </div>
            ) : (
              searchResult.map((record, index) => {
                return (
                  <div
                    key={index}
                    className="px-2 py-1 flex justify-between items-center gap-1 hover:bgLightBlue dark:hover:bgBlack2"
                  >
                    <div className="flex items-center gap-2">
                      <Image
                        src={record.img || $$.defaultUserImg}
                        className="rounded-full"
                        width="32"
                        height="32"
                        alt="Profile image"
                      />
                      <div>
                        <div>{record.name}</div>
                        <div className="text-gray-500 text-13">
                          {record.email}
                        </div>
                      </div>
                    </div>

                    {selectedId === undefined && (
                      <button
                        className="px-2.5 py-1 rounded-[20px] textBlue bgLightBlue dark:bgBlack3 hover:ring-1 hover:ring-blue-600"
                        onClick={() => setSelectedId(record.id)}
                      >
                        Add
                      </button>
                    )}
                    {selectedId === record.id && <SpinLoader size={28} />}
                  </div>
                );
              })
            )}
          </div>

          {/* Footer */}
          <div className="mt-4 pt-3 border-t border-gray-300">
            <div className="text-gray-500 text-13">
              Here you can search those teachers email who have not been
              assigned as department chairman already.
            </div>
          </div>
        </div>
      </div>
    </ModalContainer>
  );
}
