"use client";
import { Dispatch, SetStateAction, useEffect, useMemo, useState } from "react";
import Console from "@/utils/console";
import useGlobalContext, {
  countSummaryType,
  myAccessType,
  navSearchBarType,
} from "../GlobalContext";
import { Toast } from "@/utils/toastify";
import { mutate, useSWR } from "@/utils/api-handler/swr";
import api from "@/app/api/endpoints";
import IconSkeleton from "@/components/icons/IconSkeleton";
import GoogleIcons from "@/components/icons/GoogleIcons";
import SpinLoader from "@/components/preloader/SpinLoader";
import { createId, isNull } from "@/utils/more";
import SvgCat from "@/components/svg-image/Cat";
import { hideLoader } from "../BlankPageLoader";
import AddChairmanModal from "./AddChairmanModal";
import axios from "@/utils/api-handler/axios";

let cacheFacultyDeptProgramData = {
  faculty: [],
  department: [],
  program: [],
};

export default function Page() {
  const {
    myAccess,
    setActiveTab,
    doRefetch,
    countSummary,
    navSearchBar,
    setNavSearchBar,
  } = useGlobalContext();
  return useMemo(
    () => (
      <_Page
        myAccess={myAccess}
        setActiveTab={setActiveTab}
        doRefetch={doRefetch}
        countSummary={countSummary}
        navSearchBar={navSearchBar}
        setNavSearchBar={setNavSearchBar}
      />
    ),
    [
      myAccess,
      setActiveTab,
      doRefetch,
      countSummary,
      navSearchBar,
      setNavSearchBar,
    ]
  );
}

function _Page({
  myAccess,
  setActiveTab,
  doRefetch,
  countSummary,
  navSearchBar,
  setNavSearchBar,
}: {
  myAccess: myAccessType;
  setActiveTab: Dispatch<SetStateAction<string>>;
  doRefetch: boolean;
  countSummary: countSummaryType;
  navSearchBar: navSearchBarType;
  setNavSearchBar: Dispatch<SetStateAction<navSearchBarType>>;
}) {
  Console.component("Page");

  useEffect(() => {
    document.title = "Faculty";
    setActiveTab("faculty");
  }, [setActiveTab]);

  const [faculty, setFaculty] = useState<
    | undefined
    | {
        id: number;
        full: string;
        short: string;
        isEdited: boolean;
        isNew?: boolean;
      }[]
  >(undefined);
  const [department, setDepartment] = useState<
    | undefined
    | {
        id: number;
        full: string;
        short: string;
        facultyId: number;
        chairmanId: number;
        name: string;
        designation: string;
        isEdited: boolean;
        isNew?: boolean;
      }[]
  >(undefined);
  const [program, setProgram] = useState<
    | undefined
    | {
        id: number;
        full: string;
        short: string;
        departmentId: number;
        isEdited: boolean;
        isNew?: boolean;
      }[]
  >(undefined);

  // Prevent from searching
  useEffect(() => {
    if (navSearchBar.keyword) {
      Toast("This page have no search functionality");
      setNavSearchBar((prev) => ({
        ...prev,
        actualKey: "",
        debouncedKey: "",
        isLoading: false,
      }));
    }
  }, [navSearchBar.keyword, setNavSearchBar]);

  // Get faculty, department, program table data
  const [res, mutateFacultyDeptProgramData] = useSWR(
    api.readFacultyDeptProgramData
  );
  useEffect(() => {
    if (!res) return;
    if (res.status === 200) {
      setFaculty(res.data.faculty);
      setDepartment(res.data.department);
      setProgram(res.data.program);

      cacheFacultyDeptProgramData = {
        faculty: res.data.faculty,
        department: res.data.department,
        program: res.data.program,
      };
    }
    hideLoader();
  }, [res, setFaculty, setDepartment, setProgram]);

  // Reload data fetchers
  useEffect(() => {
    if (doRefetch) mutateFacultyDeptProgramData();
  }, [doRefetch, mutateFacultyDeptProgramData]);

  // Add faculty data related--->
  function addNewTableData(
    type: "faculty" | "department" | "program",
    id?: number
  ) {
    setShowSaveButton(true);
    if (type === "faculty") {
      setFaculty((prev) => [
        {
          id: createId(),
          full: "",
          short: "",
          isEdited: true,
          isNew: true,
        },
        ...(prev || []),
      ]);
    } else if (type === "department") {
      setDepartment((prev) => [
        {
          id: createId(),
          full: "",
          short: "",
          facultyId: id || 0,
          chairmanId: 0,
          name: "",
          designation: "",
          isEdited: true,
          isNew: true,
        },
        ...(prev || []),
      ]);
    } else if (type === "program") {
      setProgram((prev) => [
        {
          id: createId(),
          full: "",
          short: "",
          departmentId: id || 0,
          isEdited: true,
          isNew: true,
        },
        ...(prev || []),
      ]);
    }
  }
  // <---Add faculty data related

  // Edit faculty or department or program related--->
  const [showSaveButton, setShowSaveButton] = useState(false);

  function switchEditMode(
    type: "faculty" | "department" | "program",
    index: number
  ) {
    setShowSaveButton(true);
    if (type === "faculty")
      setFaculty((prev) =>
        prev?.map((record, i) => {
          if (i !== index) return record;
          else return { ...record, isEdited: !record.isEdited };
        })
      );
    if (type === "department")
      setDepartment((prev) =>
        prev?.map((record, i) => {
          if (i !== index) return record;
          else return { ...record, isEdited: !record.isEdited };
        })
      );
    if (type === "program")
      setProgram((prev) =>
        prev?.map((record, i) => {
          if (i !== index) return record;
          else return { ...record, isEdited: !record.isEdited };
        })
      );
  }

  function writingOnInputField(
    type: "faculty" | "department" | "program",
    index: number,
    newRecord: any
  ) {
    if (type === "faculty")
      setFaculty((prev) =>
        prev?.map((record, i) => {
          if (i !== index) return record;
          else return newRecord;
        })
      );
    if (type === "department")
      setDepartment((prev) =>
        prev?.map((record, i) => {
          if (i !== index) return record;
          else return newRecord;
        })
      );
    if (type === "program")
      setProgram((prev) =>
        prev?.map((record, i) => {
          if (i !== index) return record;
          else return newRecord;
        })
      );
  }
  // <---Edit faculty or department or program related

  // Save the updated table data
  const [isSaving, setIsSaving] = useState(false);
  async function saveUpdatedFormData() {
    setIsSaving(true);
    const res = await axios.post(api.saveUpdatedFacultyTableData, {
      faculty: faculty,
      department: department,
      program: program,
    });
    if (res.status === 200) await mutateFacultyDeptProgramData();
    setIsSaving(false);
    setShowSaveButton(false);
  }

  // Delete faculty or department or program or chairman
  const [deletingId, setDeletingId] = useState<{
    faculty: number[];
    department: number[];
    program: number[];
    chairman: number[];
  }>({
    faculty: [],
    department: [],
    program: [],
    chairman: [],
  });
  async function doDelete(
    id: number,
    type: "faculty" | "department" | "program" | "chairman"
  ) {
    setDeletingId((prev) => ({
      ...prev,
      [type]: [...prev[type], id],
    }));

    if (type === "faculty") await axios.delete(api.deleteFaculty + id);
    else if (type === "department")
      await axios.delete(api.deleteDepartment + id);
    else if (type === "program") await axios.delete(api.deleteProgram + id);
    else if (type === "chairman") await axios.delete(api.removeChairman + id);

    if (["faculty", "department", "program"].includes(type)) {
      await Promise.all([
        mutateFacultyDeptProgramData(),
        mutate(api.readCountSummary),
      ]);
    } else await mutateFacultyDeptProgramData();

    setDeletingId((prev) => ({
      ...prev,
      [type]: prev[type].filter((num) => num != id),
    }));
  }

  // Add chairman related
  const [openAddChairmanModal, setOpenAddChairmanModal] = useState(false);
  const [selectedDepartment, setSelectedDepartment] = useState({
    id: 0,
    full: "",
  });

  return (
    // Here margin bottom 64px so that save button do not hide the content of last row
    <div className="w-full h-full mb-16 px-8 py-6 bg-white dark:bgBlack2 rounded-lg">
      {/* Title */}
      <div className="mb-4 pb-3 fontGs text-2xl text-gray-600 border-b border-gray-300">
        Faculty, Department, Program
      </div>

      {/* Summary */}
      <div className="mb-4 flex justify-between items-center">
        <div className="flex items-center gap-5">
          <div className="fontGsm text-gray-500">
            Faculty {countSummary.faculty}
          </div>
          <div className="fontGsm text-gray-500">
            Department {countSummary.department}
          </div>
          <div className="fontGsm text-gray-500">
            Program {countSummary.program}
          </div>
        </div>
        <div className="pr-2">
          {myAccess?.createFaculty && (
            <IconSkeleton
              title="Add faculty"
              onClick={() => addNewTableData("faculty")}
            >
              <GoogleIcons.plus className="fill-gray-500" />
            </IconSkeleton>
          )}
        </div>
      </div>

      {faculty !== undefined && (
        <div element-name="Table" className="border border-gray-300">
          <div
            element-name="Table header"
            className="bgBlue dark:bgBlack3 text-white flex"
          >
            <div className="flex-1 px-2 py-2.5 fontGsm border-r border-gray-300">
              Faculty
            </div>
            <div className="flex-3 flex">
              <div className="px-2 py-2.5 fontGsm flex-1 border-r border-gray-300">
                Department
              </div>
              <div className="flex-1 border-r border-gray-300">
                <div className="px-2 py-2.5 fontGsm">Program</div>
              </div>
              <div className="px-2 py-2.5 fontGsm flex-1">Chairman</div>
            </div>
          </div>

          <div element-name="Table body or All faculty container">
            {isNull(faculty) ? (
              <div element-name="Row of faculty">
                <div className="w-fit h-fit mx-auto flex flex-col justify-center items-center gap-6">
                  <SvgCat className="w-[170px]" />
                  <div className="fontGsm text-gray-500">No record found</div>
                </div>
              </div>
            ) : (
              faculty.map((recordF, indexF) => {
                return (
                  <div
                    key={indexF}
                    element-name="Row of single faculty"
                    className="flex border-b border-gray-300 last:border-b-0"
                  >
                    <div
                      element-name="faculty name"
                      className="p-2 flex-1 flex items-center border-r border-gray-300  relative group"
                    >
                      {
                        // Show the actual faculty data
                        !recordF.isEdited ? (
                          <>
                            {recordF.full} ({recordF.short})
                            {
                              // Deleting loader
                              deletingId.faculty.includes(recordF.id) ? (
                                <div className="w-fit p-1 rounded-[20px] bg-white dark:bgBlack boxShadow absolute absoluteYCenter right-0 flex justify-center items-center gap-1">
                                  <SpinLoader size={28} />
                                </div>
                              ) : (
                                // Add, edit, delete option
                                (myAccess?.createFaculty ||
                                  myAccess?.editFaculty ||
                                  myAccess?.deleteFaculty) && (
                                  <div className="w-fit pl-2 pr-2.5 py-1 rounded-[20px] bg-white dark:bgBlack boxShadow absolute absoluteYCenter right-0 flex justify-center items-center gap-1 invisible group-hover:visible z-2">
                                    {myAccess?.createFaculty && (
                                      <IconSkeleton
                                        title="Add department"
                                        onClick={() =>
                                          addNewTableData(
                                            "department",
                                            recordF.id
                                          )
                                        }
                                      >
                                        <GoogleIcons.plus className="fill-gray-500" />
                                      </IconSkeleton>
                                    )}
                                    {myAccess?.editFaculty && (
                                      <IconSkeleton
                                        title="Edit faculty"
                                        onClick={() =>
                                          switchEditMode("faculty", indexF)
                                        }
                                      >
                                        <GoogleIcons.edit className="fill-gray-500" />
                                      </IconSkeleton>
                                    )}
                                    {myAccess?.deleteFaculty && (
                                      <IconSkeleton
                                        title="Delete faculty"
                                        onClick={() =>
                                          doDelete(recordF.id, "faculty")
                                        }
                                      >
                                        <GoogleIcons.delete className="fill-gray-500" />
                                      </IconSkeleton>
                                    )}
                                  </div>
                                )
                              )
                            }
                          </>
                        ) : (
                          // Show the edited faculty data
                          <div className="relative flex-1 flex flex-col gap-1">
                            <input
                              type="text"
                              placeholder="Full form"
                              className="w-full px-0.5 bgLightBlue dark:bgBlack3 border borderLightBlue focus:borderBlue"
                              value={recordF.full}
                              onChange={(e) =>
                                writingOnInputField("faculty", indexF, {
                                  ...recordF,
                                  full: e.target.value,
                                })
                              }
                            />
                            <input
                              type="text"
                              placeholder="Short form"
                              className="w-full px-0.5 bgLightBlue dark:bgBlack3 border borderLightBlue focus:borderBlue"
                              value={recordF.short}
                              onChange={(e) =>
                                writingOnInputField("faculty", indexF, {
                                  ...recordF,
                                  short: e.target.value,
                                })
                              }
                            />
                          </div>
                        )
                      }
                    </div>
                    <div
                      element-name="All department container"
                      className="flex-3 flex flex-col"
                    >
                      {department &&
                        department.map((recordD, indexD) => {
                          if (recordF.id === recordD.facultyId)
                            return (
                              <div
                                key={indexD}
                                element-name="Row of single department"
                                className="flex-1 flex border-b border-gray-300 last:border-b-0"
                              >
                                <div
                                  element-name="department name"
                                  className="p-2 flex-1 flex items-center border-r border-gray-300 relative group"
                                >
                                  {
                                    // Show the actual faculty data
                                    !recordD.isEdited ? (
                                      <>
                                        {recordD.full} ({recordD.short})
                                        {
                                          // Deleting loader
                                          deletingId.department.includes(
                                            recordD.id
                                          ) ? (
                                            <div className="w-fit p-1 rounded-[20px] bg-white dark:bgBlack boxShadow absolute absoluteYCenter right-0 flex justify-center items-center gap-1">
                                              <SpinLoader size={28} />
                                            </div>
                                          ) : (
                                            // Add, edit, delete option
                                            (myAccess?.createFaculty ||
                                              myAccess?.editFaculty ||
                                              myAccess?.deleteFaculty) && (
                                              <div className="w-fit pl-2 pr-2.5 py-1 rounded-[20px] bg-white dark:bgBlack boxShadow absolute absoluteYCenter right-0 flex justify-center items-center gap-1 invisible group-hover:visible z-2">
                                                {myAccess?.createFaculty && (
                                                  <IconSkeleton
                                                    title="Add program"
                                                    onClick={() =>
                                                      addNewTableData(
                                                        "program",
                                                        recordD.id
                                                      )
                                                    }
                                                  >
                                                    <GoogleIcons.plus className="fill-gray-500" />
                                                  </IconSkeleton>
                                                )}
                                                {myAccess?.editFaculty && (
                                                  <IconSkeleton
                                                    title="Edit department"
                                                    onClick={() =>
                                                      switchEditMode(
                                                        "department",
                                                        indexD
                                                      )
                                                    }
                                                  >
                                                    <GoogleIcons.edit className="fill-gray-500" />
                                                  </IconSkeleton>
                                                )}
                                                {myAccess?.deleteFaculty && (
                                                  <IconSkeleton
                                                    title="Delete department"
                                                    onClick={() =>
                                                      doDelete(
                                                        recordD.id,
                                                        "department"
                                                      )
                                                    }
                                                  >
                                                    <GoogleIcons.delete className="fill-gray-500" />
                                                  </IconSkeleton>
                                                )}
                                              </div>
                                            )
                                          )
                                        }
                                      </>
                                    ) : (
                                      // Show the edited department data
                                      <div className="relative flex-1 flex flex-col gap-1">
                                        <input
                                          type="text"
                                          placeholder="Full form"
                                          className="w-full px-0.5 bgLightBlue dark:bgBlack3 border borderLightBlue focus:borderBlue"
                                          value={recordD.full}
                                          onChange={(e) =>
                                            writingOnInputField(
                                              "department",
                                              indexD,
                                              {
                                                ...recordD,
                                                full: e.target.value,
                                              }
                                            )
                                          }
                                        />
                                        <input
                                          type="text"
                                          placeholder="Short form"
                                          className="w-full px-0.5 bgLightBlue dark:bgBlack3 border borderLightBlue focus:borderBlue"
                                          value={recordD.short}
                                          onChange={(e) =>
                                            writingOnInputField(
                                              "department",
                                              indexD,
                                              {
                                                ...recordD,
                                                short: e.target.value,
                                              }
                                            )
                                          }
                                        />
                                      </div>
                                    )
                                  }
                                </div>
                                <div
                                  element-name="All program container"
                                  className="flex-1 flex flex-col border-r border-gray-300"
                                >
                                  {program &&
                                    program.map((recordP, indexP) => {
                                      if (recordD.id === recordP.departmentId)
                                        return (
                                          <div
                                            key={indexP}
                                            element-name="Row of single program"
                                            className="flex-1 flex border-b border-gray-300 last:border-b-0"
                                          >
                                            <div
                                              element-name="program name"
                                              className="p-2 flex-1 flex items-center relative group"
                                            >
                                              {
                                                // Show the actual faculty data
                                                !recordP.isEdited ? (
                                                  <>
                                                    {recordP.full} (
                                                    {recordP.short})
                                                    {
                                                      // Deleting loader
                                                      deletingId.program.includes(
                                                        recordP.id
                                                      ) ? (
                                                        <div className="w-fit p-1 rounded-[20px] bg-white dark:bgBlack boxShadow absolute absoluteYCenter right-0 flex justify-center items-center gap-1">
                                                          <SpinLoader
                                                            size={28}
                                                          />
                                                        </div>
                                                      ) : (
                                                        // Edit, delete option
                                                        (myAccess?.editFaculty ||
                                                          myAccess?.deleteFaculty) && (
                                                          <div className="w-fit pl-2 pr-2.5 py-1 rounded-[20px] bg-white dark:bgBlack boxShadow absolute absoluteYCenter right-0 flex justify-center items-center gap-1 invisible group-hover:visible z-2">
                                                            {myAccess?.editFaculty && (
                                                              <IconSkeleton
                                                                title="Edit program"
                                                                onClick={() =>
                                                                  switchEditMode(
                                                                    "program",
                                                                    indexP
                                                                  )
                                                                }
                                                              >
                                                                <GoogleIcons.edit className="fill-gray-500" />
                                                              </IconSkeleton>
                                                            )}
                                                            {myAccess?.deleteFaculty && (
                                                              <IconSkeleton
                                                                title="Delete program"
                                                                onClick={() =>
                                                                  doDelete(
                                                                    recordP.id,
                                                                    "program"
                                                                  )
                                                                }
                                                              >
                                                                <GoogleIcons.delete className="fill-gray-500" />
                                                              </IconSkeleton>
                                                            )}
                                                          </div>
                                                        )
                                                      )
                                                    }
                                                  </>
                                                ) : (
                                                  // Show the edited faculty data
                                                  <div className="relative flex-1 flex flex-col gap-1">
                                                    <input
                                                      type="text"
                                                      placeholder="Full form"
                                                      className="w-full px-0.5 bgLightBlue dark:bgBlack3 border borderLightBlue focus:borderBlue"
                                                      value={recordP.full}
                                                      onChange={(e) =>
                                                        writingOnInputField(
                                                          "program",
                                                          indexP,
                                                          {
                                                            ...recordP,
                                                            full: e.target
                                                              .value,
                                                          }
                                                        )
                                                      }
                                                    />
                                                    <input
                                                      type="text"
                                                      placeholder="Short form"
                                                      className="w-full px-0.5 bgLightBlue dark:bgBlack3 border borderLightBlue focus:borderBlue"
                                                      value={recordP.short}
                                                      onChange={(e) =>
                                                        writingOnInputField(
                                                          "program",
                                                          indexP,
                                                          {
                                                            ...recordP,
                                                            short:
                                                              e.target.value,
                                                          }
                                                        )
                                                      }
                                                    />
                                                  </div>
                                                )
                                              }
                                            </div>
                                          </div>
                                        );
                                    })}
                                </div>
                                <div
                                  element-name="Chairman info"
                                  className="p-2 flex-1 flex items-center border-b border-gray-300 last:border-b-0 relative group"
                                >
                                  <div>
                                    <div>{recordD.name}</div>
                                    <div className="text-gray-500 text-xs">
                                      {recordD.designation}
                                    </div>
                                  </div>
                                  {
                                    // Deleting loader
                                    deletingId.chairman.includes(recordD.id) ? (
                                      <div className="w-fit p-1 rounded-[20px] bg-white dark:bgBlack boxShadow absolute absoluteYCenter right-0 flex justify-center items-center gap-1">
                                        <SpinLoader size={28} />
                                      </div>
                                    ) : (
                                      // Edit, delete option
                                      myAccess?.deleteFaculty && (
                                        <div className="w-fit px-2 py-1 rounded-[20px] bg-white dark:bgBlack boxShadow absolute absoluteYCenter right-0 flex justify-center items-center gap-1 invisible group-hover:visible z-2">
                                          {recordD.name ? (
                                            <IconSkeleton
                                              title="Remove chairman"
                                              onClick={() =>
                                                doDelete(recordD.id, "chairman")
                                              }
                                            >
                                              <GoogleIcons.delete className="fill-gray-500" />
                                            </IconSkeleton>
                                          ) : (
                                            myAccess?.createFaculty && (
                                              <IconSkeleton
                                                title="Add chairman"
                                                onClick={() => {
                                                  setOpenAddChairmanModal(
                                                    (prev) => !prev
                                                  );
                                                  setSelectedDepartment({
                                                    id: recordD.id,
                                                    full: recordD.full,
                                                  });
                                                }}
                                              >
                                                <GoogleIcons.plus className="fill-gray-500" />
                                              </IconSkeleton>
                                            )
                                          )}
                                        </div>
                                      )
                                    )
                                  }
                                </div>
                              </div>
                            );
                        })}
                    </div>
                  </div>
                );
              })
            )}
          </div>
        </div>
      )}

      {/* Save new or edited data */}
      {(myAccess?.createFaculty || myAccess?.editFaculty) && (
        <>
          {showSaveButton && (
            <div className="w-full h-16 fixed bottom-0 left-0 bg-white dark:bgBlack3 boxShadow flex justify-center items-center gap-2">
              <button
                className={isSaving ? "btnDisable" : "btn"}
                onClick={() => {
                  if (isSaving) return;
                  saveUpdatedFormData();
                }}
              >
                Save
              </button>
              <button
                className="btn2"
                onClick={() => {
                  setShowSaveButton(false);
                  setFaculty(cacheFacultyDeptProgramData.faculty);
                  setDepartment(cacheFacultyDeptProgramData.department);
                  setProgram(cacheFacultyDeptProgramData.program);
                }}
              >
                Cancel
              </button>
            </div>
          )}
        </>
      )}

      {/* All modal */}
      {openAddChairmanModal ? (
        <AddChairmanModal
          setOpenModal={setOpenAddChairmanModal}
          department={selectedDepartment}
        />
      ) : null}
    </div>
  );
}
